<template>
	<fx-item-row-edit
		:title="itemData.goodsDTO.name"
		@on-back="onConfirmClick"
	>
		<van-cell-group>
			<w-cell title="">{{itemData.goodsDTO.code}}</w-cell>
			<w-cell title="">{{itemData.netWeight}}</w-cell>
			<w-cell title="">{{itemData.grossWeight}}</w-cell>
			<w-cell title="">{{itemData.skinWeight}}</w-cell>
			<w-cell title="">{{itemData.weightUnit}}</w-cell>
			<w-cell
				title=""
				:is-link="!!itemData.weightPic"
				@click="onWeigthPicClick"
			></w-cell>
			<w-cell title="">{{itemData.billMoney}}</w-cell>
			<w-cell title="">{{itemData.billTaxMoney}}</w-cell>
			<w-cell title="">{{itemData.factMoney}}</w-cell>
			<w-cell title="">{{itemData.factTaxMoney}}</w-cell>
			<w-cell title="">{{itemData.diffMoney}}</w-cell>
			<w-cell title="">{{itemData.diffTaxMoney}}</w-cell>
			<w-cell title="">{{itemData.assistDiffAmoun}}</w-cell>
			<w-cell title="">{{itemData.storeHouseName}}</w-cell>
			<w-cell title="">{{itemData.batchCode}}</w-cell>
			<van-pro-field
				ref="memo"
				v-model="itemData.memo"
				:placeholder="setPlaceholder('')"
				:disabled="isDisabled"
				label=""
				input-align="right"
				@focus="onFocus('memo')"
			/>
		</van-cell-group>
		<w-button
			slot="bottom-btn"
			v-if="!isDisabled"
			size="large"
			type="primary"
			@click="onConfirmClick"
		>
		</w-button>
	</fx-item-row-edit>
</template>

<script>
export default {
	name: 'itemRowEdit',
	props: {
		visibleFlag: Boolean,
		itemData: {
			type: Object,
			default: () => {
				return {}
			}
		},
		isDisabled: Boolean
	},
	computed: {
		setPlaceholder (val) {
			return (val) => {
				return this.isDisabled ? '' : val
			}
		}
	},
	methods: {
		onCloseClick () {
			this.$emit('update:visibleFlag', false)
		},
		onConfirmClick () {
			if (this.itemData.id) {
				this.itemData.changeType = 'edit'
			}
			this.$emit('on-Confirm', this.itemData)
			this.$emit('update:visibleFlag', false)
		},
		onFocus (ref) {
			this.$fxUtils.doScrollTop(this.$refs[ref].$el)
		},
		onWeigthPicClick () {
			if (this.itemData.weightPic) {
				this.$fxImagePreview([this.itemData.weightPic])
			}
		}
	}
}
</script>
